import React from 'react';
import Pdfview from './pdfview';
import VideoTest from './video';
import './Lito.css';
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";



function Litoapp() {
  return (
    <div className='canvas'>
    
      <TransformWrapper
      initialScale={1.5}
      initialPositionX={-1800}
      initialPositionY={-100}
      minScale= {1.5}
      maxScale={3}
    >
      {({ zoomIn, zoomOut, resetTransform, ...rest }) => (
          <React.Fragment>

              <div className="tools">
                <button style={{color:"black"}} onClick={() => zoomIn()}>+</button>
                <button style={{color:"black"}} onClick={() => zoomOut()}>-</button>
                <button style={{color:"black"}} onClick={() => resetTransform()}>x</button>
              </div>
            
            <div id="canvas">
              
            <TransformComponent>

                  <div id="img"></div>

                  <div className="point-Hp_bub"></div> 
                  <div className="img-Hp_bub"></div> 
                  <div className="box-Hp_bub">
                    <div className="txt-Hp_bub"></div>
                  </div>


                  <div className="point-Hy"></div> 

                  
                  <div id="first">
                    <div>section 1</div>
                    <Pdfview />
                  </div>
                  <div id="second">
                    <div>section 2</div>
                    <VideoTest />
                  </div>
                  <div id="third">
                    <div>section 3</div>
                  </div>
                  <div id="close">
                    <div>(:</div>
                  </div>


            </TransformComponent>
            </div>

          </React.Fragment>
        )}
        </TransformWrapper>

    </div>
  );
}


export default Litoapp;